import { DefaultGearOptimizer } from '@/services/gear-optimizer';
var ACTIONS = {
  OPTIMIZE: 'optimize',
  PROGRESS: 'progress'
};

var reportProgress = function reportProgress(progress) {
  self.postMessage({
    action: ACTIONS.PROGRESS,
    result: progress
  });
};

addEventListener('message', function (event) {
  console.log('optimizer::message::start, event =', event);

  if (event.data.action == ACTIONS.OPTIMIZE) {
    var optimizer = new DefaultGearOptimizer( // const optimizer: IGearOptimizer = new FastGearOptimizer(
    event.data.store, event.data.profile, event.data.hero, reportProgress); // hack of casting self to DedicatedWorkerGlobalScope, typescript conflict between webworker and other lib

    self.postMessage({
      action: 'optimize-result',
      result: optimizer.optimize()
    });
  } else {
    // hack of casting self to DedicatedWorkerGlobalScope, typescript conflict between webworker and other lib
    self.postMessage({
      action: 'START'
    });
    self.postMessage({
      action: 'END',
      result: [1, 2, 3]
    });
  }
});